// group .. groups.. stati .. post ..group_category .. calendar
.info
	margin-top: 20px
	+clr
.info__link
	display: block
	margin-bottom: 5px
	span
		font-size: 18px
		color: $blue
		+os
	&:hover
		span
			text-decoration: underline
	&:active
		span
			color: #2693BF

.article
	background-color: #fff
	margin-bottom: 13px
	+clr
	.title__main
		margin: 5px 0 15px
.article__container
	padding: 35px 44px
	+box
	+clr
.article__text
	+clr
	p
		font-size: 18px
		color: $black
		+osl
	.article__text_smaller
		font-size: 18px

.article__img
	float: left
	max-width: 100%
	margin: 30px 15px 30px 0
	img
		max-width: 100%
.article__img_text
	margin: 50px 0
	+clr
	img
		float: left
		max-width: 66%
	p
		float: right
		width: 22%
		margin-right: 10%
		font-size: 14px
		color: #9195A1
		margin-top: 15px
.article__list
	margin: 70px 30px 55px 65px
	li
		color: $black
		font-size: 18px
		margin-bottom: 7px
		vertical-align: middle
		+osl
		i
			margin-right: 15px
			vertical-align: top

.article__picture
	float: left
	width: 105px
	height: 105px
	margin: 0 30px 10px 0
	img
		max-width: 100%
		vertical-align: top
		-webkit-border-radius: 100%
		border-radius: 100%
.article__footer
	border-top: 1px solid #ecf0f4
	background-color: #FFF
	+clr
	// .seller//++++++++
	// 	display: inline-block//++++++++
	// 	margin-bottom: 0//++++++++
	// 	transform: translate(0, -50%)//++++++++
	// 	padding-left: 45px//++++++++
	// 	margin-left: 0//++++++++
	.seller__text
		white-space: nowrap
	.seller__img:after
		content: none
	// .say//++++++++
	// 	padding-left: 45px//++++++++
	// 	transform: translate(0, -50%)//++++++++
// .article__centering//+++++
// 	margin-top: 50px//+++++
// 	transform: translate(-50%, -50%)//+++++
// 	margin-left: 50%//+++++
	// transform: translate(-50%)
.article__name
	float: left
	border-right: 1px solid #ecf0f4
	width: 23%
	height: 100px
	// padding-left: 45px
	+box
.article__tag
	position: relative
	float: left
	height: 100px
	width: 16.5%
	white-space: nowrap
	border-right: 1px solid #ecf0f4
	p
		font-size: 14px
		color: #727376
		+osl

.article__rating
	float: left
	width: 11%
	height: 100px
	white-space: nowrap
	border-right: 1px solid #ecf0f4
	&:hover,
	&.is_active
		.icon-star
			+s(icon-star-active)
		.icon-cool
			+s(icon-cool-active)
		.icon-mes
			+s(icon-mes-active)
	span
		display: inline-block
		font-size: 18px
		color: $black
		+os
		vertical-align: top
		margin-left: 8px

.article__button
	float: left
	height: 100px
	width: 25%
	white-space: nowrap
	p
		font-size: 14px
		+osl
		color: #9fa0a1
	a
		font-size: 14px
		display: block
		color: $blue
		+osl

.group__footer
	border-top: 1px solid #ecf0f4
	background-color: #FFF
	+clr
	// .article__centering//++++++++
	// 	display: inline-block//++++++++


.say
	float: left
	width: 21%
	height: 100px
	border-right: 1px solid #ecf0f4
	font-size: 14px
	color: $black
	white-space: nowrap
	+os
	p
		font-size: 14px
		color: #727376
		+osl
.article__soc
	width: 10%
	height: 100px
	border-right: 1px solid #ecf0f4
	float: left
	&:hover
		opacity: .7

.group__thame
	width: 39%
	float: left
	height: 100px

.group__thame_wrap
	white-space: nowrap
	+clr
	a
		font-size: 14px
		display: inline-block
		color: $blue
		+os
		margin-right: 10px
		padding: 1px 3px
		line-height: 1
		&:hover
			background-color: #E8ECEF

.pre-comment
	margin: 60px 0 55px
	+clr
	h4
		float: left
		font-size: 30px
		color: $black
		white-space: nowrap
		padding-left: 45px
		+osl

.pre-comment_btn
	float: right
	white-space: nowrap
	+clr
	.btn
		margin-left: 10px
		vertical-align: top


.commentar
	position: relative
	padding: 35px 0px 30px 40px
	margin-bottom: 15px
	background-color: #FFF
	+clr

.commentar__benefit,
.subcommentar__benefit
	position: absolute
	top: 35px
	right: 35px
	a
		float: left
		vertical-align: top
	span
		float: left
		margin: 0 10px
		vertical-align: top
		font-size: 15px
		color: #ADAEB3
		+osl
.icon-minus
	&:hover
		+s(icon-minus-h)

.icon-plus
	&:hover
		+s(icon-plus-h)

.subcommentar__benefit
	position: absolute
	top: 35px
	right: -80px

.commentarium
	position: relative
	padding-left: 70px
	+clr


.commentar__form
	position: relative
	top: -1px
	padding: 0
	margin: 0
	border: 0
	outline: 0
	width: 91%
	textarea
		width: 100%
		height: 203px
		border: 1px solid #e6eaed
		top: 0
		margin-right: 9%
		resize: none
		padding: 10px
		&:hover
			border-color: #c6ccd1
	.btn
		float: right
		margin: 20px 0
.commentar__user
	position: absolute
	top: 0
	left: 0
	width: 45px
	height: 45px
	vertical-align: top
	img
		max-width: 100%
		vertical-align: top
		-webkit-border-radius: 100%
		border-radius: 100%
.commentar__item
	float: left
	margin-right: 115px
	vertical-align: top
	+clr
.commentar__header
	+clr
.commentar__name
	display: inline-block
	font-size: 14px
	color: $blue
	+os
	&:hover
		color: $blued
.commentar__time
	display: inline-block
	font-size: 14px
	color: #7f8187
	+osl
.commentar__text
	font-size: 18px
	line-height: 28px
	margin: 5px 0
	color: $black
	+osl
.commentar__btn
	a
		font-size: 14px
		color: $blue
		float: left
		margin-right: 15px
		+osl
		&:hover
			color: $blued
	+clr

.subcommentar
	position: relative
	padding: 25px 0 10px 0
	+clr

.subcommentar__user
	@extend .commentar__user
	position: absolute
	top: 25px
	left: 0
.subcommentar__item
	margin-left: 70px
	+clr

.commentar__editor
	margin-right: 9%
	border: 1px solid #e6eaed
	height: 50px
	+clr
.commentar__centering
	margin-top: 25px
	-webkit-transform: translate(-50%, -50%)
	-ms-transform: translate(-50%, -50%)
	transform: translate(-50%, -50%)
	margin-left: 50%
	white-space: nowrap
	a
		display: block
.commentar__editor_one
	float: left
	height: 50px
	width: 8%
	border-right: 1px solid #e6eaed
.commentar__editor_two
	float: left
	width: 16%
	height: 50px
	border-right: 1px solid #e6eaed
	a
		display: inline-block !important
		vertical-align: top
.commentar__editor_five
	float: left
	height: 50px
	width: 27%
	border-right: 1px solid #e6eaed
	a
		vertical-align: middle
		margin-right: 20px
		display: inline-block
		&:last-child
			margin-right: 0

.state
	.article__name
		width: 28%
		p
			font-size: 14px
			color: #9FA0A1
			+osl
	.article__rating
		width: 17%
	.article__button
		width: 21%




.squard
	position: relative
	width: 100%
	background-color: #FFF
	margin-bottom: 15px
	+clr

.squard__left
	display: inline-block
	padding: 30px 80px 30px 40px
	float: left
	width: 75%
	+clr
.squard__group
	height: 100%
	float: left
	width: 37%
	.community
		text-align: left
.squard__img
	float: left
	width: 100px
	height: 100px
	margin-right: 30px
	img
		max-width: 100%
		width: 100%
		vertical-align: top
		-webkit-border-radius: 100%
		border-radius: 100%
.squard__name
	display: inline-block
	font-size: 30px
	color: $black
	+osl
	margin-top: 20px
	&:hover
		color: $blued
.squard__type
	font-size: 16px
	color: #ACB1B7
	+os
	i
		margin-right: 5px

.squard__text
	float: right
	width: 62%
	+clr
	p
		font-size: 16px
		color: #7E8489
		+os
.squard__right
	display: inline-block
	position: absolute
	top: 0
	bottom: 0
	right: 0
	width: 25%
	float: right
	border-left: 1px solid #e6eaed
	+clr
	.sideclear
		height: 100%
		+clr
		.sidehalf
			position: relative
			height: 100%
			margin-bottom: 0
		.center
			+vcenter

.target
	width: 100%
	position: relative
	margin: 70px 0 0
	+clr
	h3
		position: absolute
		top: 0
		left: 0
		max-width: 28%
		color: $black
		font-size: 24px
		+osl
.target__item
	width: 71%
	margin-left: 29%
.target__link
	font-size: 16px
	margin-right: 20px
	+os
	color: $blue
	&:hover
		color: $blued

.gad
	transform: translate(-50%, 0)



.gang
	width: 24%
	float: left
	margin-right: 1%
	margin-bottom: 10px
	position: relative
	background-color: #FFF
	padding: 30px 0 20px
	+clr
	&:nth-of-type(5)
		margin-right: 0
	.read__more
		margin-left: 40px
		color: $blue
		font-size: 16px
		margin-bottom: 12px
		width: auto !important
		+os
		&:hover
			color: $blued
		span
			vertical-align: top
.gang__header
	position: relative
	border-bottom: 1px solid #eef1f3
	padding-bottom: 20px
	padding: 0 40px 0px
	min-height: 111px

.gang__img
	position: absolute
	top: 0
	left: 35px
	width: 65px
	height: 65px
	img
		max-width: 100%
		vertical-align: top
		-webkit-border-radius: 100%
		border-radius: 100%
.gang__list
	font-size: 0
	padding: 40px 40px 0
	li
		margin-bottom: 25px
	a
		display: inline-block
		color: $black
		font-size: 16px
		+os
		&:hover
			color: $blue
.gang__row
	clear: both
	width: 100%

.mama
	margin-top: 72px

.callendar
	text-align: left
	width: 34%
	float: left
	margin: 35px 25px 25px
	.title__gang
		margin-left: 10px
.callendar__list
	font-size: 0
	+clr
	a
		float: left
		width: 12.5%
		height: 60px
		border-right: 1px solid #e7ecf1
		text-align: center
		display: block
		line-height: 60px
		font-size: 18px
		+os
		&:nth-child(9n+8)
			border-right: 0
		&:hover
			background-color: #F7F8FA
		&:active
			background-color: #EDF1F5
.calendar__row
	clear: both
	height: 1px
	background-color: #E7ECF1
.year
	text-align: left
	width: 22%
	float: right
	text-align: left
	margin-right: 40px
	margin-top: 35px
	+clr
	.title__gang
		margin-left: 10px
	a
		float: left
		line-height: 60px
		margin-right: 15px
		margin-bottom: 20px
		width: 60px
		height: 60px
		-webkit-border-radius: 100%
		border-radius: 100%
		border: 1px solid #e7eaef
		text-align: center
		font-size: 18px
		color: $black
		+os
		&:hover
			background-color: #F7F8FA
		&:active
			background-color: #EDF1F5
.callendar__orange
	color: #f4b159
.callendar__blue
	color: $blue
.callendar__fiolet
	color: #eb6c96




// КАрточка товара


.sidebox__delivery
	h6
		font-size: 14px
		color: $black
		line-height: 1.8
		+os
	p,
	li
		position: relative
		font-size: 14px
		color: $black
		+osl
	li
		margin-left: 12px
		&:before
			content: "-"
			position: absolute
			top: 0
			left: -10px


.good__info
	border-bottom: 1px solid #e3e7eb
	margin: 20px 0 40px
	a
		display: inline-block
		margin-right: 45px
		line-height: 50px
		font-size: 16px
		color: $black
		+osl
		&:hover
			color: $purpur
		&.is_active
			position: relative
			&:after
				content: ""
				position: absolute
				bottom: -2px
				left: 0
				right: 0
				width: 100%
				height: 4px
				background-color: $purpur


.view
	width: 46%
	float: left
	position: relative
	border-right: 1px solid #f3f5f7
	padding-bottom: 28px
	+clr
.view__color
	text-align: left
	margin: 25px 25px 10px
	+clr
.view__color_info
	float: left
	display: block
	height: 40px
	width: 43px
	overflow: hidden
	margin-right: 10px
	padding: 3px 5px
	-webkit-border-radius: 35px
	border-radius: 35px
	border: 1px solid #e1e7ec
	+tr(.5s)
	+clr
	&:hover
		.color__text
			color: $purpur
	&.is_active
		width: auto
.color__text
	line-height: 30px
	vertical-align: top
	display: inline-block
	margin: 0 10px
	color: #23252A
	font-size: 12px
	+osl

.view__img
	position: relative
	width: 100%
	min-height: 480px
	padding: 0 25px
	+clr
	img
		max-width: 100%
		+vcenter

.view__preview
	padding: 10px 25px 0
	+clr
.view__preview-img
	float: left
	margin-right: 5px
	width: 65px
	height: 70px
	&.is_active
		border-bottom: 4px solid $purpur
		opacity: 0.5
	img
		max-width: 100%
		max-height: 100%
		vertical-align: top


.configure
	width: 54%
	float: right
.configure__header
	width: 100%
	height: 70px
	border-bottom: 1px solid #E1E7EC
	+clr
.configure__header_in
	position: relative
	width: 33%
	float: left
	height: 70px
	border-right: 1px solid #e1e7ec
.configure__header_center
	+vcenter
	width: 100%
.configure__header_has
	width: 80%
	height: 33px
	white-space: nowrap
	span
		display: inline-block
		margin-left: 10px
		line-height: 30px
		font-size: 12px
		color: $black
		vertical-align: top
		+osl
.configure__header_rate
	position: relative
	width: 67%
	float: left
	height: 70px
	+clr
.configure__header_box
	white-space: nowrap
	+clr
	.stars
		float: left
		margin-left: 20px
	p
		margin-left: 10px
		float: left
	.new
		top: 0
		left: auto
		right: 30px

.configure__price
	text-align: left
	height: 135px
	padding-top: 30px
	border-bottom: 1px solid #e1e7ec
	+clr
	button
		float: left
		margin-left: 10px
		margin-top: 10px
		background-color: $purpur
		color: #fff
		height: 50px
		-webkit-border-radius: 50px
		border-radius: 50px
		padding: 0 30px
		font-size: 20px
		+osl
		&:hover
			background-color: #df326b
		&:active
			-webkit-box-shadow: 0 0 10px rgba(255, 255, 255, .6) inset
			box-shadow: 0 0 10px rgba(255, 255, 255, .6) inset
		i
			margin-right: 7px

.configure__price_item
	width: 42%
	float: left
	padding-left: 20px
	s
		display: block
		font-size: 20px
		color: #adb1bc
		+osb
		sub
			font-size: 15px
			text-transform: lowercase
			+osl
	strong
		font-size: 40px
		color: $black
		+osb
		span
			font-size: 32px
			text-transform: lowercase
			+osl
			color: $black


.configure__check
	height: 110px
	border-bottom: 1px solid #f3f5f7
	+clr
.configure__check_item
	height: 110px
	position: relative
	width: 33.2%
	float: left
	position: relative
	border-right: 1px solid #f3f5f7
	&:last-child
		border-right: 0
	&:hover
		span
			text-decoration: underline
.configure__check_center
	+vcenter
	text-align: center
	width: 100%
	i
		display: block
		margin: 0 auto 10px
	span
		font-size: 14px
		color: $blue
		+osl

.configure__type
	text-align: left
	padding: 46px 30px 40px
	p
		color: #979FA6
		font-size: 16px
		margin-bottom: 10px
		+osl
		span
			color: $black
			margin-left: 5px



.js_half_more
	+ .read__more
		position: relative
		z-index: 10
		padding-top: 5px
		-webkit-box-shadow: 0 -10px 20px 0 #fff
		box-shadow: 0 -10px 20px 0 #fff
		span
			color: $blue
			font-size: 16px
			margin-left: 10px
			+osl


.show__comment
	padding: 30px
	background-color: #F7F8F9
	margin-bottom: 5px
	.stars
		float: left
		margin-right: 10px
	p
		font-size: 18px
		color: $black
		+osl

.more__comments_get
	display: block
	margin: 30px 0 0px
	text-align: center
	white-space: nowrap
	height: 30px
	span
		display: inline-block
		color: $blue
		font-size: 16px
		vertical-align: top
		+osl
	i
		margin-right: 10px
.function__heder
	width: 50%
	float: right
	position: absolute
	top: 33px
	right: 50px
	border-bottom: 1px solid #f3f5f7
	height: 50px
	a
		display: inline-block
		line-height: 50px
		position: relative
		font-size: 16px
		margin-right: 15px
		color: $black
		+osl
		&:hover
			color: $blue
		&.is_active::after
			content: ""
			position: absolute
			left: 0
			height: 5px
			width: 100%
			background-color: $purpur
			bottom: -2px

.function__name
	width: 100%
	p
		width: 50%
		float: left
		font-size: 16px
		color: #979FA6
		+osl
		margin-bottom: 10px
	.function__get
		float: right
		color: $black